.box-cover {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}


.box-wrapper {
    width: 250px;
    border-radius: 10px;
    box-shadow: 5px 5px 10px gray;
    margin: 20px;
}

.box-info {
    padding: 10px;
}

.box-wrapper img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 10px;
}

.title,
.description,
.price,
.rating {
    margin: 5px 0;
}

.price {
    color: crimson;
}

.form-parent input{
    display: block;
    width: 50%;
    padding: 10px;
    margin: 10px 0;
}

.box-wrapper{
    position: relative;
}

.box-wrapper .close{
    position: absolute;
    bottom: 5%;
    right: 10%;
}